import { KV } from './KV.js';
export { KV };

export class WebStorageKV extends KV {

    #storage;
    #prefix;
    #channel;

    constructor({
        storage = 'local',              // 'local' | 'session' | Storage instance
        channel = null,
        ...options
    } = {}) {
        super(options);
        this.#storage =
            typeof storage === 'string'
                ? (storage === 'session' ? window.sessionStorage : window.localStorage)
                : storage;
        this.#prefix = this.path.join(':');
        if (channel) {
            this.#channel = new BroadcastChannel(channel);
        }
    }

    #fullKey(key) { return `${this.#prefix}:${key}`; }

    #ownsStorageKey(storageKey) { return storageKey?.startsWith(this.#prefix + ':'); }

    #access(key) {
        const fullKey = this.#fullKey(key);
        const raw = this.#storage.getItem(fullKey);
        if (raw == null) return;

        let node;
        try {
            node = this._deserialize(raw);
        } catch {
            // Corrupt/unexpected value -> treat as absent
            this.#storage.removeItem(fullKey);
            return;
        }

        if (this._expired(node)) {
            this.#storage.removeItem(fullKey);
            return;
        }

        return node;
    }

    #saveNode(key, node) {
        const fullKey = this.#fullKey(key);
        this.#storage.setItem(fullKey, this._serialize(node));
    }

    /* ---------- public API ---------- */

    async close() {
        this.#channel?.close();
        await super.close();
    }

    async count() { return (await this.keys()).length; }

    async keys() { return (await this.#entries()).map(([k]) => k); }

    async values() { return (await this.#entries()).map(([, v]) => v); }

    async entries() { return await this.#entries(); }

    async #entries(dump = false) {
        const out = [];
        for (let i = 0; i < this.#storage.length; i++) {
            const storageKey = this.#storage.key(i);
            if (!this.#ownsStorageKey(storageKey)) continue;

            const key = storageKey.slice(this.#prefix.length + 1);
            const node = this.#access(key);
            if (!node) continue;

            out.push([key, dump ? node : node.value]);
        }
        return out;
    }

    async has(key) {
        key = typeof key === 'object' && key ? key.key : key;
        return !!this.#access(key);
    }

    async get(key) {
        const isSelector = typeof key === 'object' && key;
        key = isSelector ? key.key : key;

        const node = this.#access(key);
        if (!node) return;

        return isSelector ? node : node.value;
    }

    async set(key, value) {
        let rest, event;
        ({ key, value, rest, event } = this._resolveSet(key, value));

        this.#saveNode(key, { value, ...rest });

        // Fire events locally + across tabs
        this.#channel?.postMessage(event);
        await this._fire(event);
    }

    async delete(key) {
        key = typeof key === 'object' && key ? key.key : key;

        this.#storage.removeItem(this.#fullKey(key));

        const event = {
            type: 'delete',
            key,
            path: this.path,
            origins: this.origins,
            timestamp: Date.now(),
        };

        this.#channel?.postMessage(event);
        await this._fire(event);
    }

    async clear() {
        // Remove only keys within this KV path prefix
        for (let i = this.#storage.length - 1; i >= 0; i--) {
            const k = this.#storage.key(i);
            if (this.#ownsStorageKey(k)) {
                this.#storage.removeItem(k);
            }
        }

        const event = {
            type: 'clear',
            path: this.path,
            origins: this.origins,
            timestamp: Date.now(),
        };

        this.#channel?.postMessage(event);
        await this._fire(event);
    }

    async json(arg = null, options = {}) {
        if (arg && arg !== true) {
            let { data, event } = this._resolveInputJson(arg, options);

            if (!options.merge) {
                for (let i = this.#storage.length - 1; i >= 0; i--) {
                    const k = this.#storage.key(i);
                    if (this.#ownsStorageKey(k)) {
                        this.#storage.removeItem(k);
                    }
                }
            }

            for (const [key, node] of Object.entries(data)) {
                const storedNode = { ...node };
                this.#saveNode(key, storedNode);
            }

            this.#channel?.postMessage(event);
            await this._fire(event);
            return;
        }

        return Object.fromEntries(await this.#entries(arg));
    }
}
